import React from 'react';
import Task from '../Task/Task'
 
  const TaskList = (props) => {
    const tasks = props.todos.map(el => {
      return (
          <Task
            task={el.description}
            identifier={el.id}
            key={el.id}
            onTaskDelete={props.onTaskDelete}
          />
      )
    })

    return (
      <ul className="todo-list">
        {tasks}
      </ul>
    )
  }

  export default TaskList