import React, {Component} from 'react';


export default class Task extends Component{
    state = {
        status: 'task'
    }
    onTaskEdit = () => {
        this.setState((state) => {
            return {
                status: "editing"
            }
        })
    }
    onTaskComplete = () => {
        this.setState((state) => {
            return {
                status: state.status === "completed" ? "task" : "completed"
            }   
        })
    }
    render(){
        const {task, identifier} = this.props
        return (
            <li className={this.state.status} key={identifier}>
                <div className="view">
                    <input className="toggle" type="checkbox" onClick={this.onTaskComplete}/>
                    <label>
                        <span className="description">
                            {task}
                        </span>
                        <span className="created">
                        </span>
                    </label>
                    <button className="icon icon-edit" onClick={this.onTaskEdit}/>
                    <button 
                        className="icon icon-destroy"
                        onClick={() => this.props.onTaskDelete(identifier)}
                    />
                </div>
                {this.state.status === 'editing' ? 
                        <input 
                            type="text" 
                            className="edit" 
                            defaultValue={task}
                            onKeyDown={this.onTaskEdit}
                        />
                        : null}
            </li>
        )}
}

